import React, { useState} from "react";
import axios from 'axios'; // Importez axios ici
import "./index.css";
import Ad from "../../elements/ad/Ad";
import ListeCategorie from "../../card/listbox/ListCategorie.js";
import Quotes from "../../elements/quote/Quote.js";
import Input from "../../elements/input/Input.js";
import BtnprimaryComponent from "../../elements/button/BtnPrimary.js";



const CategorieBoard = () => {

  const [nom, setNom] = useState("");

  const handleNomChange = (event) => {
    setNom(event.target.value);
  };
  const handleCreation = async () => {

    try {
      const response = await axios.post("https://cropfarmback-production.up.railway.app/categories-culture", {
          nomcategorie:nom
      });

      console.log("Nouvelle plante cre avec succs :", response.data);
      window.location.href = "/categories";
      // Mettre  jour l'interface ou rediriger l'utilisateur vers une autre page si ncessaire
    } catch (error) {
      alert("Erreur lors de la cration de la plante veuillez ressayer ^^");
      console.error("Erreur lors de la cration de la plante :", error);
    }
  
  };

  return (
  <div className="container">
    <div className="background-board">
        <ListeCategorie />
  
        <div class="grid-container">
            <div class="item item1"><Ad/></div>
            <div class="item item2">
            <div className="float-bloc-list">
              <div className="greet" >
                  <div className="title">Insrer une nouvelle catgorie</div>
                  <div className="subtitle">Soyez inspirs donnez nous un nom!</div>
              </div>
                <Input type="text" placeholder="Nom de la catgorie" value={nom} onChange={handleNomChange}/>
                <BtnprimaryComponent holder="Crer" onclick={handleCreation}/>
            </div>
            </div>
            <div class="item item3"><Quotes/></div>
        </div>
        
    </div>
    
  </div>
);
};

export default CategorieBoard;
