import React from "react";
import axios from 'axios';
import "./index.css"; // Assurez-vous d'importer votre fichier CSS pour les styles
// import axios from 'axios';

const DeleteComponent = ({ id, deletetable,redirect }) => {

  const handleClick = async () => {
    try {
      let request = "https://cropfarmback-production.up.railway.app/";
      request+= deletetable;
      request+=id;
      // Mettez  jour la valeur 'corbeille'  1 en utilisant une requte PUT
      const response = await axios.put(request, {
          corbeille: 1
      });

      console.log("La valeur 'corbeille' a t mise  jour :", response.data);
      window.location.href = redirect;
      // Si vous avez besoin de mettre  jour l'tat ou de dclencher d'autres actions aprs la mise  jour, faites-le ici
      
    } catch (error) {
      console.error("Erreur lors de la mise  jour de la valeur 'corbeille' :", error);
    }
  };

  return (
    <button className="delete-button" onClick={handleClick}>
      <div className="delete-icon">
        <div className="line"></div>
        <div className="line"></div>
      </div>
    </button>
  );
};

export default DeleteComponent;
