import React, { useEffect, useState } from "react";
import axios from 'axios'; // Importez axios ici
import "./index.css";
import CardPhoto from "../../card/card-photo/CardPhoto.js";

const TerrainBoard = () => {
  const [terrainsValides, setTerrainsValides] = useState([]);

  useEffect(() => {
    // Rcuprer le nom d'utilisateur depuis le localStorage lors du montage du composant
    let storedId = localStorage.getItem("userId");
    let request = "https://cropfarmback-production.up.railway.app/terrains/proprietaire/valid/";
    request += storedId;
    
    console.log(request);

    axios.get(request)
    .then(response => {
      // Mettez  jour l'tat avec les donnes rcupres depuis l'API
      setTerrainsValides(response.data);
    })
    .catch(error => {
      console.log('Erreur lors de la rcupration des terrains non valides :', error);
    });

    
  }, []);

  return (
      <div className="container">
      <div className="background-terrain-card" >

      <div className="contain-card-pic">
      {terrainsValides.map(terrain => (
          <CardPhoto id={terrain.id_terrain} desc={terrain.desc_terrain} coord={terrain.coord_location}/>
      ))}
      </div>

      </div>
      
      <div className="greeting" >      
      <h1 className="title">Liste de vos terrains valids</h1>
        <p className="subtitle">Voici les terrains qui sont en votre possession cher propritaire</p>
      </div>
    </div>
  );

}

export default TerrainBoard;
